}

main.dark-theme .user-tools__btn {
  fill: #fff;
}

main.dark-theme .user-tools__btn:hover {
  fill: #ff6b08;
}

.menu-tags {
  width: auto;
  z-index: 10;
  opacity: 0;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), background-color .3s cubic-bezier(.4, 0, .2, 1), opacity .3s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  right: 0;
  overflow-x: hidden;
  transform: translateX(100%);
  box-shadow: 0 4px 8px rgba(0, 0, 0, .04), 0 2px 4px rgba(0, 0, 0, .08), 0 1px 3px rgba(0, 0, 0, .16);
}

.menu-tags.is-open {
  max-width: 100%;
  height: auto;
  opacity: 1;
  visibility: visible;
  margin: 0 auto;
  display: block;
  position: static;
  transform: translateX(0);
}

main.dark-theme .menu-tags.is-open {
  background-color: rgba(255, 107, 8, .835);
}

.menu-tags__list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.menu-tags__item {
  padding: 10px 8px;
}

@media screen and (max-width: 767px) {
