}

.user-tools__btn--menu {
  transform-style: preserve-3d;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1);
}

.user-tools__btn--menu.is-open {
  transform: rotateY(-180deg);
}

.menu-close-svg {
  display: none;
}

.user-tools__btn--menu.is-open .menu-close-svg {
  display: block;
}

.menu-open-svg {
  transform: rotateY(0deg);
}

.user-tools__btn--menu.is-open .menu-open-svg {
  display: none;
}

.sun {
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  transform: scale(0);
}

.user-tools__btn--toggle.sun .sun {
  transform: scale(1);
}

.moon {
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  transform: scale(0);
}

.user-tools__btn--toggle.moon .moon {
  transform: scale(1);
}

.user-tools__btn--toggle {
  transform-style: preserve-3d;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
  transform: scale(1);
}

